"use strict";

var display1El = document.querySelector(".display-1");
var display2El = document.querySelector(".display-2");
var tempResultEl = document.querySelector(".temp-result");
var numbersEl = document.querySelectorAll(".number");
var operationEl = document.querySelectorAll(".operation");
var equalEl = document.querySelector(".equal");
var clearAllEl = document.querySelector(".all-clear");
var clearLastEl = document.querySelector(".last-entity-clear");
var dis1Num = "";
var dis2Num = "";
var result = null;
var lastOperation = "";
var haveDot = false;
// =>
numbersEl.forEach(function (number) {
  number.addEventListener("click", function (e) {
    if (e.target.innerText === "." && !haveDot) {
      console.log("e: ", e);
      console.log("e.target: ", e.target);
      console.log("e.target.innerText: ", e.target.innerText);
      haveDot = true;
    } else if (e.target.innerText === "." && haveDot) {
      return;
    }
    dis2Num += e.target.innerText;
    display2El.innerText = dis2Num;
  });
});
// =>
operationEl.forEach(function (operation) {
  operation.addEventListener("click", function (e) {
    if (!dis2Num) return;
    haveDot = false;
    var operationName = e.target.innerText;
    if (dis1Num && dis2Num && lastOperation) {
      mathOperation();
    } else {
      result = parseFloat(dis2Num);
    }
    clearVar(operationName);
    lastOperation = operationName;
    console.log(result);
  });
});
// =>
function clearVar() {
  var name = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : "";
  dis1Num += dis2Num + " " + name + " ";
  display1El.innerText = dis1Num;
  display2El.innerText = "";
  dis2Num = "";
  tempResultEl.innerText = result;
}
// =>
function mathOperation() {
  if (lastOperation === "x") {
    result = parseFloat(result) * parseFloat(dis2Num);
  } else if (lastOperation === "+") {
    result = parseFloat(result) + parseFloat(dis2Num);
  } else if (lastOperation === "-") {
    result = parseFloat(result) - parseFloat(dis2Num);
  } else if (lastOperation === "/") {
    result = parseFloat(result) / parseFloat(dis2Num);
  } else if (lastOperation === "%") {
    result = parseFloat(result) % parseFloat(dis2Num);
  }
}
// =>
equalEl.addEventListener("click", function () {
  if (!dis2Num || !dis1Num) return;
  haveDot = false;
  mathOperation();
  clearVar();
  display2El.innerText = result;
  tempResultEl.innerText = "";
  dis2Num = result;
  dis1Num = "";
});
// =>
clearAllEl.addEventListener("click", function () {
  dis1Num = "";
  dis2Num = "";
  display1El.innerText = "";
  display2El.innerText = "";
  result = "";
  tempResultEl.innerText = "";
});
// =>
clearLastEl.addEventListener("click", function () {
  display2El.innerText = "";
  dis2Num = "";
});
// =>
window.addEventListener("keydown", function (e) {
  if (e.key === "0" || e.key === "1" || e.key === "2" || e.key === "3" || e.key === "4" || e.key === "5" || e.key === "6" || e.key === "7" || e.key === "8" || e.key === "9" || e.key === ".") {
    clickButtonEl(e.key);
  } else if (e.key === "+" || e.key === "-" || e.key === "/" || e.key === "%") {
    clickOperation(e.key);
  } else if (e.key === "*") {
    clickOperation("x");
  } else if (e.key == "Enter" || e.key === "=") {
    clickEqual();
  }
});
// =>
function clickButtonEl(key) {
  numbersEl.forEach(function (button) {
    if (button.innerText === key) {
      button.click();
    }
  });
}
// =>
function clickOperation(key) {
  operationEl.forEach(function (operation) {
    if (operation.innerText === key) {
      operation.click();
    }
  });
}
// =>
function clickEqual() {
  equalEl.click();
}